import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController} from 'ionic-angular';
import {CountryProvider} from '../../providers/country/country';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import {RestProvider} from '../../providers/rest/rest';

@Component({
  selector: 'page-iban',
  templateUrl: 'iban.html'
})
export class IbanPage {

  countries: any;
  country: any;
  errorMessage: string;
  iban: string;
  isvalid: boolean;
  show:boolean
  bankName: string;
  bankCity: string;
  bankBic: string;
  credentialsForm: FormGroup;
  constructor(public navCtrl: NavController,public restProvider: RestProvider, public countriesProvider: CountryProvider, public navParams: NavParams, private formBuilder: FormBuilder, public alertCtrl: AlertController) {
    this.isvalid=false;
    this.show=false;
    this.credentialsForm = this.formBuilder.group({
      pays: ['',Validators.required],
      iban: ['',Validators.required]
    });
  }
  ionViewDidLoad() {
    //this.getIbanCheck();
    this.getCountries();

  }

  getCountries() {
    this.countriesProvider.getCountries()
       .subscribe(
         countries => this.countries = countries,
         error =>  this.errorMessage = <any>error);
  }
  getCountryByName(url: string) {
    this.countriesProvider.getCountryByName(url)
       .subscribe(
         country => this.country = country,
         error =>  this.errorMessage = <any>error);
  }
  verifier(){
    this.isvalid=false;
    this.show=false;
    this.isvalid= false;
    let pays=this.credentialsForm.controls['pays'].value;
    let codeiban=this.credentialsForm.controls['iban'].value;
    let apiUrl ='//192.168.43.210:9988/rest/iban/'+pays+''+codeiban;
    this.restProvider.getIbanCheck(apiUrl)
       .subscribe(
         iban => {
           this.iban= iban['iban'];
           this.isvalid= iban['valid'];
           this.show=true;
           this.bankName= (iban['bankData'])['name'];
           this.bankCity= (iban['bankData'])['city'];
           this.bankBic= (iban['bankData'])['bic'];
           if(!this.isvalid){
             this.showAlert(pays+''+codeiban);
           }
         },
         error =>  this.errorMessage = <any>error);
    this.getCountryByName('https://restcountries.eu/rest/v2/alpha/'+pays);
  }

  showAlert(iban: string) {
    let alert = this.alertCtrl.create({
      title: 'Invalide Iban!',
      subTitle: 'L\'iban "'+iban+'" que vous avez saisi n\'est pas valide , veuillez le corriger svp !',
      buttons: ['OK']
    });
    alert.present();
  }

}



